:root{
    --main-width: calc(100vw - var(--toolbar-height) - 1px);
    --main-height: calc(100vh - var(--toolbar-height) - 1px)
}

html{
    scroll-behavior: smooth;
}
body{
    &.loading{
        #cover{
            position:fixed;
            z-index:1000;
            --spineWidth: 80px;
        }
      //   #cover > *{
      //       opacity:0;
      //   }
        #cover .spine, #cover .stripe{
            opacity:1;
        }
        #cover .stripe{
            width:var(--spineWidth);
            left: 50%;
            transform: translate(-50%);
            animation: loading 1s linear both;
        }
        #cover .spine{
            border-right:1px solid white;
        }
    }

    &.stuck{
        // height: 100vh;
        overflow: hidden;
        #cover .spine{
            border-left:1px solid white;
        }
    }
    
}

.lot-wrapper{
   // display: none;
   visibility: hidden;
   &.visible{
      visibility: visible;
      // display: block;
   }
}
/* Hide scrollbar for Chrome, Safari and Opera */
*::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
* {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.hide-vis{opacity:0;pointer-events: none;}

.thumbnail-lockup{
    left: -100vw;
}
.thumbnail-lockup.loaded{
    left:auto;
}

body.visible-false > *:not(.password-modal){
    display:none;
}
body.visible-false{
    overflow:hidden;
}
.password-modal{display:none}
body.visible-false .password-modal{
    position:fixed;
    display:block;
    top:0px;
    left:0px;
    background:black;
    width:100vw;
    height:100vh;
    z-index:10000;
    text-align:center;
    color:white;
    line-height:1.2;
    font-size:18px;
    button{
        border-radius:5px;
        padding:10px 24px 7px;
        background-color:var(--swann-blue);
    }
    input[type="text"]{
        padding:10px 10px 7px;
        margin-right:10px;
        border:0px;
        border-radius:5px;
        font-weight:400;
        font-size:14px;
        font-family:var(--sans-serif);
        margin-bottom:20px;
    }
    label{
        font-weight:400;
        font-size:14px;
    }
}

@keyframes shake {
  0% { transform: translate(1px, 1px) rotate(0deg); }
  10% { transform: translate(-1px, -2px) rotate(-1deg); }
  20% { transform: translate(-3px, 0px) rotate(1deg); }
  30% { transform: translate(3px, 2px) rotate(0deg); }
  40% { transform: translate(1px, -1px) rotate(1deg); }
  50% { transform: translate(-1px, 2px) rotate(-1deg); }
  60% { transform: translate(-3px, 1px) rotate(0deg); }
  70% { transform: translate(3px, 1px) rotate(-1deg); }
  80% { transform: translate(-1px, -1px) rotate(1deg); }
  90% { transform: translate(1px, 2px) rotate(0deg); }
  100% { transform: translate(1px, -2px) rotate(-1deg); }
}

@keyframes loading{
    0%{width: var(--spineWidth)}
    100%{width:100vw}
}

body{
    background:#111111;
    font-family: var(--serif);
    cursor:url(../img/cursor.svg), auto;
}
mark{
    // background-color:#f7fab9;
    // padding: 0.2em 0em;
    background-color: transparent;
    padding: 0.25em 0em;
    background-color:rgba(255,255,0,0.6);
    cursor:url(../img/type-x-cursor.svg), auto;
    // border-bottom: 2.5px dotted #0d9aff;
}
section.lot .center-column p{
    cursor:url(../img/type-cursor.svg), auto;
}

::selection { 
    background-color:yellow;
}
textarea {
  resize: none;
}
.small-type{
    font-family:var(--sans-serif);
    font-weight:bold;
    font-size:15px;
}
#cursor{
    position:fixed;
    z-index:1000;
    width:18px;
    pointer-events:none;
    left:-100px;
    top:-100px;
}


section{
    position:relative;
}
#topbar{
    position:fixed;
    height:var(--toolbar-height);
    line-height:calc(var(--toolbar-height) + 2px);
    border-bottom:1px solid white;
    top:0px;
    left:0px;
    width:100vw;
    background:black;
    z-index:100;
    transform:translateY(calc(-100% - 2px));
    transition:transform 300ms;
    .catalogue-title{
        padding-left: calc(var(--toolbar-height) + 15px);
        font-size: 1.2rem;
        width: calc(100vw - 186px - var(--toolbar-height) - 30px);
        height: 100%;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    .topbar--dot{
        display:inline-block;
        margin:0px 10px;
        font-size:1.3rem;
    }
    .buttons{
        position:fixed;
        right:0px;
        top:0px;
        a, button {
            padding:0px 26px;
            border-left:1px solid white;
            height:100%;
            display:inline-block;
            line-height: var(--toolbar-height);
        }
    }
}
#topbar.visible{
    transform:translateY(0%);
}
#searchbar{
    -webkit-appearance: none;
    appearance: none;
    width: 100% ;
    height: var(--toolbar-height);
    position: fixed;
    background: white;
    top: var(--toolbar-height);
    transform:translateY(-200%);
    transition:transform 300ms;
    z-index: 99;
    border: 0px;
    border-bottom: 1px solid grey;
    font-family:var(--sans-serif);
    font-weight:bold;
    font-size:15px;
    box-sizing: border-box;
    padding: 0 10px;
    color: var(--swann-blue);
}
#searchbar.visible, #searchbar.visible + .searchBtns{
    transform:translateY(0px);
}
#searchbar:focus{
    outline:none;
    border:none;
    border-bottom: 1px solid grey;
    border-radius: 0px;
}
#searchbar::placeholder{
    font-family:var(--sans-serif);
    font-weight:bold;
    font-size:15px;
    color:lightgrey;
}
.searchBtns{
    height:calc(var(--toolbar-height) + 1px);
    top: calc(var(--toolbar-height) - 1px);
    position: fixed;
    right:0px;
    color:black;
    z-index: 99;
    font-family:var(--sans-serif);
    font-weight:bold;
    font-size:15px;
    transform:translateY(-200%);
    transition:transform 300ms;
}
.searchBtns button{
    border-left:1px solid black;
    padding:0px 20px;
    height: 100%;
}
.thumbnail-lockup{
    visibility:hidden;
    opacity:0;
    position:absolute;
    // left: -100vw;
    align-items:flex-start;
    align-content:flex-start;
    max-width:200px;
    pointer-events: none;
    // transition:opacity 300ms;
    display:flex;
    img{
        width:50px;
        height: auto;
        margin-right:10px;
    }
    p{
        margin:0px;
        background:rgba(0,0,0,0.5);
    }
}

.thumbnail-lockup.bottom{
    bottom:0px;
}
.thumbnail-lockup.right{
    transform: translateX(-100%);
    text-align: right;
    flex-direction: row-reverse;
    img{
        margin-right:0px;
        margin-left:10px;
    }
}
.sidebar{
    width: var(--toolbar-height);
    background-color:tomato;
    border-right:1px solid white;
    position:fixed;
    bottom:0px;
    left:0px;
    height:var(--main-height);
    text-align:center;
    display:flex;
    flex-direction:column;
    z-index:3;
    #hamburger, #infoBtn{
        padding-top:12px;
        display:inline-block;
    }
    #infoBtn{
        padding-bottom:9px;
    }
    #marker{
        display:inline-block;
        height:2px;
        width:100%;
        background:black;
        position:absolute;
        top:0%;
        left:0px;
        display:none;
    }
    .sidebar--lots{
        height: calc(100% - 40px);
        padding: 20px 0px;
        position:relative;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        a{
            flex: 1;
        }
        li{
            width:100%;
            background:transparent;
            width: 100%;
            background: transparent;
            // height: 50px;
            // flex: 1;
            // display:flex;
            .thumbnail-lockup{
                max-width:calc(var(--toolbar-height) + 310px);
                text-align:left;
                transition:0s;
                pointer-events:none;
                img {
                    padding-left:calc(var(--toolbar-height) + 10px);
                }
                p{  
                    width: 300px;
                    padding-top:5px;
                    background:transparent;
                }
            }
            
        }
        @media screen and (min-width: 726px){
            a:hover li{
                border-top:1px solid white;
                .thumbnail-lockup, a{
                    visibility:visible;
                    opacity:1;
                    position: fixed;
                    z-index: 10;
                }
                &:after{
                    content:'';
                    background:black;
                    opacity:0.5;
                    display:block;
                    position:fixed;
                    width:var(--main-width);
                    height:var(--main-height);
                    top:var(--toolbar-height);
                    left:var(--toolbar-height);
                    pointer-events:none;
                }
            }
        }
        .sidebar-chapter-separation{
            border-top:1px solid white;
            position:relative;
        }
        .sidebar-chapter-separation > li{
            pointer-events:none;
            visibility:hidden;
            opacity:0;
            position:absolute;
            width:400px;
            text-align:left;
            left: calc(var(--toolbar-height) + 10px);
            top: -8px;
            p{margin:0px;}
            .chapter-title{font-size: 1.2rem;}
        }
        a.sidebar-chapter-separation:hover li{
            visibility: visible;
            opacity: 1;
            border-top:0px;
            p{
                z-index: 10;
                position: relative;
            }
        }
        a:hover li::after{
            content:'';
            background:black;
            opacity:0.5;
            display:block;
            position:fixed;
            width:var(--main-width);
            height:var(--main-height);
            top:var(--toolbar-height);
            left:var(--toolbar-height);
            pointer-events:none;
        }
        
        li.bookmarked{
            border-top:1px solid var(--swann-blue);
        }
        li.bookmarked.current, li.current{
            border-top:2px solid black;
        }
        
    }
    .sidebar--lots:hover .thumbnail-lockup{
        left:auto;
    }
}
.main-content{
    // width:var(--main-width);
    width: 100%;
    padding-left:calc(var(--toolbar-height) + 1px);
    box-sizing: border-box;
    // margin-top:calc(var(--toolbar-height) + 1px);
    background:black;
}

#cover{
    // margin-top:calc(var(--toolbar-height) + 1px);
    --stripeHeight: 80px;
    --coverMargin: 40px;
    --spineWidth: 0px;
    height:100vh;
    width:100%;
    background:black;
    position:relative;
    border-bottom:1px solid white;
    z-index:4;
    display:flex;
    > * {
        transition: opacity 300ms;
    }
    .logo{
        position: absolute;
        top:calc(var(--stripeHeight)/2);
        // left:calc(var(--coverMargin));
        left:var(--coverMargin);
        transform: translateY(-50%);
        width:170px;
    }
    .left, .right{
        position:absolute;
        bottom:0px;
    }
    .right{
        right:0px;
        top:calc(var(--stripeHeight)*2);
        padding:40px;
        width:calc(50% - var(--spineWidth)/2 - var(--coverMargin)*2 - 1px);
        min-height:calc(100% - var(--stripeHeight)*2 - var(--coverMargin)*2);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        overflow: scroll;
    }
    .left{
        left:0px;
        width:calc(50% - var(--spineWidth)/2 - 1px);
        height:calc(100% - var(--stripeHeight)*2 - 2px);
    }
    .spine{
        width:var(--spineWidth);
        transition: 500ms;
        // transition-delay:500ms;
        border-left:1px solid white;
        border-right:0px solid white;
        height: 100%;
        position:absolute;
        left:50%;
        transform:translate(-50%);
        top:0px;
        overflow: hidden;
        .spine-text{
            width: calc(100vh - var(--stripeHeight)*2 - var(--coverMargin)*2);
            height: var(--spineWidth);
            line-height: var(--spineWidth);
            background: black;
            padding:0px var(--coverMargin);
            position: absolute;
            left: 50%;
            top: calc(50% + var(--stripeHeight)/2 + 2px);
            display: flex;
            transform: translateX(-50%) rotate(90deg);
            .spine-title, .spine-date{
                font-weight:400; margin:0px;
                text-align:center;
                width:100%;
                overflow: visible;
                white-space: nowrap;
            }
            .spine-title{font-size: 1.6rem;flex:4;text-overflow: ellipsis;overflow:hidden}
            .spine-date{font-size: 1.2rem;flex:1;margin-right: 15px;}
        }
    }
    .left img{
        width:100%;
        height: 100%;
        // height:100vh;
        // bottom:0px;
        object-fit: cover;
        object-position: top center;
    }
    .stripe{
        height:var(--stripeHeight);
        width:100%;
        background:tomato;
        border-top:1px solid white;
        border-bottom:1px solid white;
        position:absolute;
        top:var(--stripeHeight);
    }
    h1{
        font-family: 'JJannon Display', serif;
        font-size: 100px;
        line-height: 90px;
        font-weight:300;
        margin:10px 0px;
        max-width:750px;
        // overflow: hidden;
        text-overflow: ellipsis;
    }
    h1.smaller{
        font-size:70px;
        line-height:70px;
    }
    h2{
        font-weight:400;
        // position:absolute;
        bottom:0px;
        margin: var(--coverMargin) 0px;
        line-height:1.2;
    }
    h1, h2, h3{
        user-select:none;
    }
    .cover-date{
        margin-bottom:0px;
    }
}
.on-the-cover{
    font-weight:400;
    position:absolute;
    // margin: var(--coverMargin) 0px;
    top:0px;
    left:calc(50% + var(--spineWidth)/2 + var(--coverMargin));
    // left:var(--coverMargin);
    color:#888888;
    height: var(--stripeHeight);
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.phantom-toc{position:relative;height:150vh;width:calc(100vw - var(--toolbar-height) - 1px);}
#table-of-contents{
    position:fixed;
    top:0px;
    right:0px;
    height:100vh;
    // width:calc(100vw - var(--toolbar-height) - 1px);
    padding-left:var(--toolbar-height);
    left: 0;
    width: 100%;
    z-index:3;
    border-bottom:1px solid white;  
    #toc-dates{

    }
    #toc-dots{
        display:flex;
        flex-direction: column;
        justify-content: space-between;
        height:calc(var(--main-height) - var(--toolbar-height)*2 - 4px - 20px);
        position:absolute;
        bottom: var(--toolbar-height);
        width: calc(var(--main-width) - 15px);
        box-sizing: border-box;
        padding: 10px 0px 10px 10px;
        a > p{margin:5px 0px;}
        a > p:before{
            content: '';
            width: calc(var(--toolbar-height) + 5px);
            height: 1.2px;
            background-color: white;
            z-index: 100;
            display: block;
            position: relative;
            top: 7px;
            left: calc(var(--toolbar-height)*-1 - 10px);
        }
        .chapter-title{
            font-size:1.2rem;
        }
        .small-type{
            margin-bottom:0px;
        }
        .small-type + .chapter-title {
            margin-top: 0px;
        }
        .small-type + .chapter-title:before {
            display:none;
        }
        .dot-container{
            position:relative;
            // IF YOU DONT WANT THE LINE-BY-LINE HOVER, COMMENT OUT THIS LINE:
            // flex: 1; 
            .dot-container-inner{
                position:absolute;
                // width:200px;
                // width:100%;
            }
        }
        .dot-container:hover, .dot-container.hover {
            z-index:10;
            // border-top:1px solid white;
            .dot:hover + .thumbnail-lockup{
                visibility:visible;
                opacity:1;
            }
            .dot-container-inner{
                width:200px;
            }
        }
        .dot-container:hover .line{
            content: '';
            width: 100vw;
            height: 1.2px;
            background-color: white;
            z-index: 100;
            display: block;
            position: absolute;
            top: 0px;
            left: calc(var(--toolbar-height)*-1 - 10px);
        }
    }
    .dot{
        background:white;
        width:8px;
        height:8px;
        border-radius:50%;
        border:0.05px solid grey;
        margin-bottom:4px;
    }
    .dot.square, .dot.square.diamond{border-radius:0;  transform: scale(0.9)}
    .dot.diamond{
        border-radius:0;
        transform: rotate(45deg) skew(10deg, 10deg);
    }
    .dates{
        display:flex;
        position:absolute;
        bottom:0px;
        width:calc(100% - 20px);
        left: calc(var(--toolbar-height) + 1px);
        // line-height: var(--toolbar-height);
        justify-content:space-between;
        padding: 10px;
        background:#1a1a1a;
        p{
            // flex:1;
            // width:100%;
            margin:0px;
            pointer-events:none;
        }
    }
}
.subnav{
