}

#hamburgerCallout:before{
    content: '';
    background-image: url('../img/callout-arrow.svg');
    position:absolute;
    left:-16px;
    width:16px;
    height:16px;
    background-size:contain;
}

#arrowsCallout{
    position:fixed;
    bottom:-200px;
    right:20px;
    top:inherit;
    left:inherit;
    background:white;
    border-radius: 8px;
    padding:10px 20px;
    z-index:100000;
    #arrow-keys {
        margin-bottom:12px;
        div:first-child{
            justify-content:center;
            span {
                background:#efefef;
                svg{transform:rotate(90deg);}
            }
        }
        div{
            display:flex;
            justify-content:space-between;
            width:100%;
            height:44px;
            margin-bottom:4px;
            span{
                display:block;
                width:44px;
                height:44px;
                background:var(--swann-blue);
                border-radius:8px;
