            display:inline-block;
            margin-left:10px;
        }
        button{
            border-radius:5px;
            padding:10px 24px 7px;
            margin-right:10px;
            margin-bottom:35px;
        }
        button#printBtn{background-color:var(--swann-blue);}
        button#cancelBtn{background-color:#2F2F2F}
    }

    .control-group{
        margin-bottom:50px;
    }
    .control {
        display: block;
        position: relative;
        // padding-left: 30px;
        margin-bottom: 10px;
        // padding-top: 6px;
        cursor: pointer;
        font-size: 1.2rem;
        line-height: 37px;
    }
    .control input {
        position: absolute;
        z-index: -1;
        opacity: 0;
    }
    .control_indicator {
        position: absolute;
        top: 12px;
        left: -32px;
        height: 10px;
        width: 10px;
        background: black;
        // border: 0px solid #000000;
        border-radius: undefinedpx;
    }
    .control:hover input ~ .control_indicator,
    .control input:focus ~ .control_indicator {
        background: var(--swann-blue);
    }

    .control input:checked ~ .control_indicator {
        background: #0099ff;
    }
    .control:hover input:not([disabled]):checked ~ .control_indicator,
    .control input:checked:focus ~ .control_indicator {
        background: var(--swann-blue);
    }
    // .control input:disabled ~ .control_indicator {
    //     background: #e6e6e6;
    //     opacity: 0.6;
    //     pointer-events: none;
    // }
    .control_indicator:after {
        box-sizing: unset;
        content: '';
        position: absolute;
        display: none;
    }
    .control input:checked ~ .control_indicator:after {
        display: block;
    }
    .control-radio .control_indicator {
        border-radius: 50%;
    }

    .control-radio .control_indicator:after {
        left: 0px;
        top: 0px;
        height: 0px;
        width: 0px;
        border-radius: 50%;
        background: var(--swann-blue);
        transition: background 250ms;
    }
    .control-radio input:disabled ~ .control_indicator:after {
        background: #7b7b7b;
    }.control-radio .control_indicator::before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        width: 4.5rem;
        height: 4.5rem;
        margin-left: -1.3rem;
        margin-top: -1.3rem;
        background: var(--swann-blue);
        border-radius: 3rem;
        opacity: 0.6;
        z-index: 99999;
        transform: scale(0);
    }
    .control-radio input + .control_indicator::before {
        animation: s-ripple 250ms ease-out;
    }
    .control-radio input:checked + .control_indicator::before {
        animation-name: s-ripple-dup;
    }

}


