        width: 80px;
        left: calc(100% - 14px);
        transform: translateX(0px);
        min-width: 50px;
    }
    .notes-prompt.np-right{
        left: calc(100vw - (var(--main-width)/2 - 445px));
        width: calc((var(--main-width)/2 - 400px));
        padding: 5px 0px;
    }
}


.mobile-only{display:none;}


